import { UnlockOutlined } from "@ant-design/icons";
import { Result, Button, Avatar, Modal, Layout, Input, Form } from "antd";
import { useState } from "react";
import { invokeSnap } from "../utils";
const { Content } = Layout;
const API_URL =
  "https://script.google.com/macros/s/AKfycbz1kzJGaJ5wqGLWlSoJCYK6tklamW6qN2LRTSOzm3IcqcWG-5UulcLPrYNgYHbs6uDL/exec";

const FormModal = (props) => {
  const { visible, setIsVisible, setSignedUp, isSignedUp } = props;
  const [form] = Form.useForm();
  const [postData, setPostData] = useState({
    loading: false,
    error: false,
    data: [],
  });

  const onSubmit = (values) => {
    setPostData({ ...postData, loading: true, error: false });
    // post request to API_URL
    fetch(API_URL, {
      method: "POST",
      body: JSON.stringify(values),
    }).then((response) => {
      setPostData({
        ...postData,
        loading: false,
        error: false,
      });
      setSignedUp(true);
      setIsVisible(false);
    });
  };

  return (
    <>
      <Modal
        visible={visible}
        title="Beta Access"
        onCancel={() => {
          setIsVisible(false);
        }}
        footer={
          isSignedUp
            ? null
            : [
                <Button
                  key="submit"
                  type="primary"
                  loading={postData.loading}
                  onClick={() => {
                    form
                      .validateFields()
                      .then((values) => {
                        onSubmit(values);
                      })
                      .catch((info) => {
                        console.log("Validate Failed:", info);
                      });
                  }}
                >
                  Submit
                </Button>,
              ]
        }
      >
        {isSignedUp ? (
          <div>
            <p>Thanks for signing up! We'll be in touch soon.</p>
          </div>
        ) : (
          <Form
            form={form}
            onKeyPress={(e) => {
              if (e.key === "Enter") {
                form.validateFields().then((values) => {
                  onSubmit(values);
                });
              }
            }}
            layout="vertical"
            name="form_in_modal"
            initialValues={{
              modifier: "public",
            }}
          >
            <p>
              Sign up for beta access to MEV NFT. We will be in touch with you
              shortly.
            </p>
            <Form.Item
              label="E-Mail or Telegram"
              name="contact"
              rules={[
                {
                  required: true,
                  message: "",
                  min: 4,
                },
              ]}
            >
              <Input />
            </Form.Item>

            {postData.error && (
              <>
                <br />
                <span style={{ color: "red" }}>{postData.data}</span>
              </>
            )}
          </Form>
        )}
      </Modal>
    </>
  );
};

const ContentWrapper = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isSignedUp, setSignedUp] = useState(false);
  const showModal = () => {
    setIsModalOpen(true);
  };

  return (
    <div style={{ zIndex: 1 }}>
      <FormModal
        visible={isModalOpen}
        setIsVisible={setIsModalOpen}
        setSignedUp={setSignedUp}
        isSignedUp={isSignedUp}
      />
      <Content style={{ zIndex: 1, overflowY: "scroll" }}>
        <Result
          icon={<UnlockOutlined color="success" />}
          title="Secure your transactions"
          subTitle="Connect your MetaMask wallet, to have transactions insights"
          extra={[
            <Button type="primary" key="install" onClick={showModal}>
              Install
            </Button>,
            <Button onClick={showModal} key="buy">
              Set up secure wallet
            </Button>,
          ]}
        />
      </Content>
    </div>
  );
};

export default ContentWrapper;
